
.pub-guide {
	min-width: 1280px;

	&-header {
		z-index: 500;
		box-sizing: border-box;
		position: sticky;
		top: 0;
		display: flex;
		align-items: center;
		width: 100%;
		height: 50px;
		padding: 0 30px;
		background: #e78753;

		.tit {
			flex-shrink: 0;
			font-size: 20px;
			color: #fff;
		}
	}

	&-table {
		table-layout: fixed;
		border-collapse: collapse;
		width: 100%;
		margin: 5px 0 10px;
		border-bottom: 1px solid #ccc;

		tr {
			&.line {
				border-top: 2px solid #ccc;
			}
		}

		thead {
			th {
				padding: 7px 5px;
				background: #d9d9d9;
				border: 1px solid #ccc;
				border-bottom: double #ccc;
				white-space: nowrap;
				font-size: 13px;
				font-weight: normal;
			}
		}

		tbody {
			th {
				padding: 10px;
				border: 1px solid #ccc;
				font-size: 13px;
				color: #555;
				font-weight: normal;
				text-align: left;
			}

			td {
				padding: 10px;
				border: 1px solid #ccc;
				font-size: 12px;
				color: #555;

				&>a {
					display: inline-block;
					color: #e07941;
					letter-spacing: 0.5px;
					text-decoration: underline;
				}

				.yet,
				.ing,
				.hold,
				.end {
					display: inline-flex;
					flex-shrink: 0;
					width: 70px;
					height: 24px;
					line-height: 24px;
					font-size: 13px;
					letter-spacing: 0.2px;
					font-weight: normal;
					border-radius: 15px;

					&::before {
						content: '';
						width: 100%;
						text-align: center;
					}
				}

				.yet {
					background: #eaeaea;
					color: #333;

					&::before {
						content: '작업전';
					}
				}

				.ing {
					background: #5193ff;
					color: #fff;

					&::before {
						content: '작업중';
					}
				}

				.hold {
					background: #b5b5b5;
					color: #fff;

					&::before {
						content: '작업홀딩';
					}
				}

				.end {
					background: #e07941;
					color: #fff;

					&::before {
						content: '작업완료';
					}
				}

				.pop {
					display: flex;
					align-items: center;
					margin: 5px 0;
					line-height: 1;
					color: #e07941;
					gap: 0 5px;

					&::before {
						content: 'P';
						width: 18px;
						height: 18px;
						line-height: 18px;
						border-radius: 50%;
						border: 1px solid #e07941;
						background: #fff;
						font-size: 12px;
						color: #e07941;
						font-weight: 600;
						text-align: center;
					}
				}
			}
		}

		.ac {
			text-align: center !important;
		}
	}

	&-tab {
		z-index: 500;
		position: sticky;
		top: 50px;
		display: flex;
		align-items: center;
		width: 100%;
		border-bottom: 1px solid #ccc;
		background: #fff;

		.p-tab {
			padding: 10px 0;
			font-size: 18px;
			color: #8f959e;
			margin: 0 15px;
			background: #fff;
			cursor: pointer;

			&.act {
				color: #333;
				box-shadow: inset 0 -4px 0 0 #e07941;
			}
		}
	}
}
